*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  line-height: 1;
  background-color: #fff;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent; }

:focus {
  outline: none; }

p,
dd,
dl,
figure,
blockquote {
  margin: 0; }

blockquote,
q {
  quotes: none; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit; }

audio,
video {
  display: block; }

img {
  display: inline-block;
  border: none; }

iframe {
  border: none; }

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit; }

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit; }

abbr {
  border: none;
  text-decoration: none; }

b,
strong {
  font-weight: inherit; }

i,
em {
  font-style: inherit; }

dfn {
  font-style: inherit; }

mark {
  background-color: transparent;
  color: inherit; }

small {
  font-size: inherit; }

button,
input,
optgroup,
select,
textarea {
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background-color: transparent;
  font: inherit;
  color: inherit;
  letter-spacing: inherit; }

input[type='text'],
textarea {
  appearance: none; }

button,
input {
  overflow: visible; }

button,
select {
  text-align: left;
  text-transform: none; }

button,
[type='button'],
[type='reset'],
[type='submit'] {
  cursor: pointer;
  appearance: none; }

textarea {
  resize: none;
  overflow-y: auto;
  overflow-x: hidden; }

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border: none;
  padding: 0; }

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: none; }

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto; }

[type='search']::-webkit-search-decoration {
  appearance: none; }

[type='search'] {
  outline: none; }

fieldset {
  padding: 0;
  margin: 0;
  border: none; }

legend {
  display: block;
  padding: 0;
  white-space: normal; }

select {
  appearance: none; }

select::-ms-expand {
  display: none; }

::-webkit-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity .3s; }

::-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity .3s; }

:-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity .3s; }

:-ms-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity .3s; }

:focus::-webkit-input-placeholder {
  opacity: 0; }

:focus::-moz-placeholder {
  opacity: 0; }

:focus:-moz-placeholder {
  opacity: 0; }

:focus:-ms-input-placeholder {
  opacity: 0; }

svg {
  display: inline-block;
  width: 100%;
  height: 100%; }

[hidden] {
  display: none; }

:disabled,
.disabled {
  cursor: not-allowed; }

::-ms-clear {
  display: none; }

.clearfix::after {
  content: '';
  display: table;
  clear: both; }

.visually-hidden {
  position: absolute;
  z-index: -1;
  width: 0;
  height: 0;
  margin: 0;
  border: none;
  overflow: hidden; }

.btn-primary {
  display: inline-block;
  padding: 5px 9px;
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
  background-image: linear-gradient(#ff0600 30%, #d40300); }
  .btn-primary:hover {
    background-image: linear-gradient(#d40300 30%, #ff0600); }

.header {
  padding: 68px 0; }
  .header .container {
    display: flex;
    align-items: center; }
    @media (max-width: 425px) {
      .header .container {
        flex-wrap: wrap; } }

.footer {
  padding: 26px 0 34px 0; }
  .footer .container {
    display: flex;
    justify-content: space-between; }
    @media (max-width: 900px) {
      .footer .container {
        flex-wrap: wrap; } }

.footer-title {
  margin-bottom: 16px;
  color: #ffb500; }

@media (max-width: 900px) {
  .footer-inf {
    margin-bottom: 40px; } }

.footer-connect {
  flex-basis: 775px; }
  @media (max-width: 900px) {
    .footer-connect {
      flex-basis: 100%; } }

.form {
  padding: 10px;
  background-color: #2c3945; }

.form-rows {
  margin-bottom: 10px; }

.form-row:not(:last-child) {
  margin-bottom: 10px; }

.form-col input, .form-col textarea {
  display: inline-block;
  width: 100%;
  background-color: #fff; }

.form-col input {
  padding: 16px 11px; }

.form-col textarea {
  padding: 10px 11px; }

.form-btn-submit {
  float: right;
  padding: 14px 56px;
  font-size: 18px; }

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  font: 14px/1 arial, sans-serif, monospace;
  color: #000;
  background-color: #132330; }

h1 {
  font-weight: bold;
  font-size: 24px;
  line-height: 1; }

h2 {
  font-weight: bold;
  font-size: 24px;
  line-height: 1; }

h3 {
  font-weight: bold;
  font-size: 24px;
  line-height: 1; }

h4 {
  font-weight: bold;
  font-size: 24px;
  line-height: 1; }

h5 {
  font-weight: bold;
  font-size: 24px;
  line-height: 1; }

h6 {
  font-weight: bold;
  font-size: 24px;
  line-height: 1; }

.container {
  width: 95%;
  max-width: 1120px;
  margin: 0 auto; }

.logo {
  flex-basis: auto;
  width: 250px;
  padding-right: 44px; }

.tagline {
  font-size: 25px;
  color: #fff; }
  @media (max-width: 425px) {
    .tagline {
      flex-basis: 100%;
      margin-top: 40px; } }

.main {
  flex: 1; }

.sect-title {
  text-transform: uppercase;
  color: #ff0600; }

.news {
  padding-top: 48px;
  background-color: #fff; }

.news-title {
  margin-bottom: 10px; }

.news-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

@media (max-width: 900px) {
  .news-col {
    flex-basis: 100%; } }

@media (min-width: 900px) {
  .news-col {
    flex-basis: 48%; } }

@media (min-width: 1200px) {
  .news-col {
    flex-basis: 345px; } }

.article {
  padding: 16px 0; }
  .article .helper {
    display: flex;
    flex-wrap: wrap; }
    @media (min-width: 600px), (max-width: 600px) {
      .article .helper {
        min-height: auto; } }
    @media (min-width: 900px) {
      .article .helper {
        min-height: 140px; } }
    @media (min-width: 1200px) {
      .article .helper {
        min-height: 174px; } }

.article-pic-wrap {
  flex-basis: auto;
  position: relative;
  z-index: 11;
  width: 120px;
  height: 120px;
  overflow: hidden; }
  .article-pic-wrap img {
    position: absolute;
    top: -15%;
    left: -30%;
    z-index: 1;
    width: 200%; }
    @media (max-width: 425px) {
      .article-pic-wrap img {
        width: 100%;
        left: 0; } }
  @media (max-width: 425px) {
    .article-pic-wrap {
      width: 100%;
      min-height: 150px; } }

.article-name {
  margin-bottom: 14px;
  font-size: 18px; }
  @media (max-width: 425px) {
    .article-name {
      margin: 14px 0; } }

.article-cont {
  flex: 1;
  padding-left: 20px; }
  @media (max-width: 425px) {
    .article-cont {
      padding: 0; } }

.article-btn-container {
  flex-basis: 100%;
  margin-top: 18px; }

.offers {
  padding: 47px 0 54px;
  background-color: #2c3945; }

.offers-title {
  margin-bottom: 50px; }

.offers-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

@media (min-width: 600px), (max-width: 600px) {
  .offers-col {
    flex-basis: 100%; }
    .offers-col:not(:last-child) {
      margin-bottom: 34px; } }

@media (min-width: 900px) {
  .offers-col {
    flex-basis: 48%; }
    .offers-col:not(:nth-last-child(-n+2)) {
      margin-bottom: 34px; } }

@media (min-width: 1200px) {
  .offers-col {
    flex-basis: 250px; }
    .offers-col:not(:nth-last-child(-n+4)) {
      margin-bottom: 34px; } }

.offer {
  position: relative;
  z-index: 111;
  display: block;
  overflow: hidden; }
  .offer:hover::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 11;
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); }
  @media (min-width: 600px), (max-width: 600px) {
    .offer {
      padding-top: 190px; } }
  @media (min-width: 900px) {
    .offer {
      padding-top: 188px; } }
  @media (min-width: 1200px) {
    .offer {
      padding-top: 127px; } }
  .offer img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%; }

.inf {
  flex-basis: 250px; }
  .inf li {
    color: #fff; }
    .inf li:not(:last-child) {
      margin-bottom: 10px; }
    .inf li span {
      font-weight: bold; }
